.ProfilePicker {
  box-sizing: border-box;
  color: #a8a9ad;
  height: 200px;
  transition: 300ms ease-in-out;
  width: 150px;

  &:hover {
    transform: scale(1.2);
  }

  &__title {
    font-weight: 600;
    margin: 15px 0;
    text-align: center;
  }

  &__button {
    align-items: center;
    background-color: #a8a9ad;
    border-radius: 50%;
    cursor: pointer;
    display: flex;
    justify-content: center;
    margin: 35px auto;
    height: 75px;
    width: 75px;
  }

  &__icon-plus {
    width: 25px;
    height: 25px;
  }

  &--add {
    border: 1px solid #5a5a5a;
    border-radius: 5px;
  }
}
